/* Project Grid */
#project-grid {
    width: 75%;
    margin: auto auto 50px auto;
    min-height: 1000px;
    display: grid;
    grid-template-columns: 4;
    grid-template-rows: 5;
}

#project-grid div {
    margin: 5px;
    background-image: url('../static/images/test_grid_image1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

#project-grid-1 {
    grid-row: 1/3;
    grid-column: 1/3;
    border: 1px solid #707070;
}

#project-grid-2 {
    grid-row: 1/1;
    grid-column: 3/5;
    border: 1px solid #707070;
}

#project-grid-3 {
    grid-row: 3/5;
    grid-column: 1/3;
    border: 1px solid #707070;
}

#project-grid-4 {
    grid-row: 2/3;
    grid-column: 3/5;
    border: 1px solid #707070;
}

#project-grid-5 {
    grid-row: 3/5;
    grid-column: 3/3;
    border: 1px solid #707070;
}

#project-grid-6 {
    grid-row: 3/6;
    grid-column: 4/4;
    border: 1px solid #707070;
}

#project-grid-7 {
    grid-row: 5/5;
    grid-column: 1/4;
    border: 1px solid #707070;
}

.static-project {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-bottom: 30px;
}

#image-carousel-container img {
    max-height: 300px;
    max-width: 400px;
    min-height: 250px;
    min-width: 220px;
}

#button-container button {
    border: none;
    background-color: transparent;
}

#button-container button:focus {
    border: none;
    outline: none;
}

#button-container button ion-icon {
    font-size: 25px;
}

@media screen and (max-width: 880px) {
    #project-grid {
        width: 75%;
        margin: auto auto 50px auto;
        min-height: 1000px;
        display: grid;
        grid-template-columns: 4;
        grid-template-rows: 5;
    }

    #project-grid-5 {
        grid-row: 3/5;
        grid-column: 3/5;
        border: 1px solid #707070;
    }

    #project-grid-6 {
        grid-row: 5/5;
        grid-column: 3/5;
        border: 1px solid #707070;
    }

    #project-grid-7 {
        grid-row: 5/5;
        grid-column: 1/3;
        border: 1px solid #707070;
    }
}

@media screen and (max-width: 660px) {
    #project-grid {
        width: 75%;
        margin: auto auto 50px auto;
        min-height: 1000px;
        display: grid;
        grid-template-columns: 1;
        grid-template-rows: 7;
    }

    #project-grid-1 {
        grid-row: 1/2;
        grid-column: 1/2;
        border: 1px solid #707070;
    }
    
    #project-grid-2 {
        grid-row: 2/3;
        grid-column: 1/2;
        border: 1px solid #707070;
    }
    
    #project-grid-3 {
        grid-row: 3/4;
        grid-column: 1/2;
        border: 1px solid #707070;
    }
    
    #project-grid-4 {
        grid-row: 4/5;
        grid-column: 1/2;
        border: 1px solid #707070;
    }

    #project-grid-5 {
        grid-row: 5/6;
        grid-column: 1/2;
        border: 1px solid #707070;
    }

    #project-grid-6 {
        grid-row: 6/7;
        grid-column: 1/2;
        border: 1px solid #707070;
    }

    #project-grid-7 {
        grid-row: 7/8;
        grid-column: 1/2;
        border: 1px solid #707070;
    }
}
