/* Top Container */
#top-container {
    padding: 150px 50px 50px 50px;
    min-height: 500px;
}

#top-container-header {
    font-size: 50px;
    font-weight: bold;
}

#top-container img {
    height: 50%;
    width: 100%;
    margin: auto;
    position: relative;
    opacity: 0;
    transition: 0.7s;
}

#top-container div:nth-child(1) {
    width: 75%;
    margin: auto;
}

#main-image-text {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate( -50%, -50% );
    color: white;
    font-size: 50px;
    font-weight: bold;
    text-shadow: 1px 1px 4px rgba(0,0,0,0.76);
    opacity: 0;
    transition: 0.7s;
}

#top-image {
    background-image: url('../static/images/main.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 75%;
    margin: auto;
    height: 500px;
    min-width: 225px;
    min-height: 200px;
    opacity: 0;
    transition: 0.7s;
}

/* Reviews Container */
#review-circles {
    display: flex;
    justify-content: center;
}

#review-circles div {
    background-color: #707070;
    border-radius: 50px;
    width: 30px;
    height: 30px;
    margin: 10px;
}

#reviews-section a {
    color: #007bff;
}

#reviews-section a:hover {
    text-decoration: none;
}

#review-section a:focus {
    color: #007bff;
}

#reviews-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.customer-review {
    max-width: 350px;
    width: 350px;
    padding: 10px;
    background-color: white;
    color: gray;
    margin: 10px;
    border: 1px solid gray;
}

/* Contact Container */

#contact-container {
    height: 1100px;
    background-image: url('../static/images/wood-background.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}

#contact-form-button {
    background-color: hsla(0,100%,0%,0.4);
    color: white;
    border: 1px solid hsla(0,100%,0%,0.4);
    width: 150px;
    padding: 10px;
}

#contact-form-button:hover {
    opacity: 0.8;
    transition: 0.3s;
    border: 1px solid white;
}

#contact-form-button:focus {
    outline: none;
}


#contact-container-header {
    font-size: 90px; 
    padding-top: 100px; 
    color: white; 
    font-weight: bold;
}

#form-background {
    max-width: 1200px;
    background-color: white; 
    margin: auto;
    padding: 80px;
}

#contact-form-container {
    max-width: 1000px;
    height: 550px;
    background-color: hsla(0,100%,0%,0.1); 
    margin: auto;
    box-shadow: 0px 13px 37px -17px rgba(0,0,0,0.75);
}

#contact-form {
    height: 100%; 
    text-align: left;
    width: 700px;
    margin: auto;
    padding-top: 50px;
}

#message-text-area {
    width: 280px; 
    height: 150px;
    padding: 5px
}

.form-input {
    outline: none;
    width: 250px;
    padding: 5px;
}

.contact-form-table-cell {
    display: table-cell; 
    padding: 10px 10px 10px 70px;
}

/* Footer */

#footer-icon {
    transition: 0.3s;
    height: 30px; 
    width: 30px;
    float: right;
    margin-left: 20px;
}

#footer-icon:hover {
    transition: 0.3s;
    background-color: white;
}

.sectionDivider {
    background-color: grey;
    height: 20px;
    margin: 0px 50px 0px 50px;
}

.footer{
    height: 200px;
    background-color: grey;
}

.social-icon {
    font-size: 30px;
    margin: 10px;
    color: rgb(27, 27, 27);
}

.social-icon:hover {
    cursor: pointer;
}

label {
    margin-bottom: 0px;
}

#third-container-image {
    height: 400px; 
    width: 600px;
}

#text-box {
    color: white;
    background-color: #707070; 
    height: 300px; 
    font-size: 35px; 
    margin: 50px 10px 50px 10px;
}

#text-box div {
    padding: 60px; 
    height: 100%;
}

#text-box p {
    font-weight: bold;
}

@media screen and (max-width: 2000px) {
    #form-background {
        margin-top: 120px;
    }
}

@media screen and (max-width: 1545px) {
    #contact-container-header {
        padding-top: 10px;
    }
}

@media screen and (max-width: 1375px) {
    #third-container-image {
        height: 300px;
        width: 500px;
    }
}

@media screen and (max-width: 900px) {
    .contact-form-table-cell {
        padding: 0px;
        display: block;
        width: 100%;
    }

    .form-input {
        width: 100%;
    }

    #message-text-area {
        width: 100%;
        height: 100px;
    }

    #contact-form-container {
        height: 580px;
    }

    #contact-form {
        padding: 5px;
        width: 100%;
    }

    .table-row {
        display: block;
        width: 100%;
    }

    #contact-container-header {
        padding-top: 0px;
        font-size: 65px;
    }

    #top-container-header {
        font-size: 50px;
        font-weight: bold;
    }
}

@media screen and (max-width: 880px) {
    #main-image-text {
        font-size: 40px;
    }
}

@media screen and (max-width:710px) {
    #third-container-image {
        width: 100%;
    }
}

@media screen and (max-width:670px) {
    #main-image-text {
        font-size: 30px;
    }
}

@media screen and (max-width: 640px) {

    #top-container-content {
        display: none;
    }

    #top-cotainer-header {
        display: none;
    }

    #third-container {
        padding: 150px 0px 0px 0px;
    }

    #third-container-image {
        height: 300px;
    }

    #third-container-header {
        font-size: 45px;
    }
}

@media screen and (max-width: 585px) {
    #contact-container-header {
        font-size: 50px;
        padding-top: 40px;
    }
}

@media screen and (max-width: 550px) {
    #top-container-header {
        font-size: 30px;
        font-weight: bold;
    }
}

@media screen and (max-width:530px) {
    #main-image-text {
        font-size: 25px;
    }
}

@media screen and (max-width: 485px) {
    #form-background {
        padding: 20px;
    }
}

@media screen and (max-width: 475px) {
    #third-container {
        padding: 0px;
    }

    #third-container-image {
        height: 300px;
    }

    #third-container-header {
        font-size: 35px;
    }
}

@media screen and (max-width:460px) {
    #main-image-text {
        font-size: 20px;
    }
}

@media screen and (max-width: 395px) {
    #text-box div {
        padding: 40px;
    }
}

@media screen and (max-width: 385px) {
    #form-background {
        padding: 10px;
        height: auto;
    }

    #contact-container {
        height: 900px;
    }

    #contact-container-header {
        font-size: 40px;
        padding-top: 20px;
    }
}

@media screen and (max-width: 355px) {
    #text-box div {
        padding: 30px;
    }
}